import { doc, onSnapshot } from "firebase/firestore";
import React, { useContext, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../context/AuthContext";
import { ChatContext } from "../context/ChatContext";
import { db } from "../firebase";

function Chats(props) {
  const navigate = useNavigate();
  const [chats, setChats] = useState([]);
  const { data } = useContext(ChatContext);

  const { currentUser } = useContext(AuthContext);
  const { dispatch } = useContext(ChatContext);
  useEffect(() => {
    const getChats = () => {
      const unsub = onSnapshot(doc(db, `userChats`, currentUser.uid), (doc) => {
        setChats(doc.data());
      });
      return () => {
        unsub();
      };
    };
    currentUser.uid && getChats();
  }, [currentUser.uid]);

  const handleSelect = (u) => {
    dispatch({ type: "CHANGE_USER", payload: u });
    navigate("/chat");

  };

  const open = (p) => {
    // console.log(p);
    for (
      let i = 0;
      i < Object.entries(chats)?.sort((a, b) => b[1].date - a[1].date).length;
      i++
    ) {
      if (
        Object.entries(chats)?.sort((a, b) => b[1].date - a[1].date)[i][0] === p
      ) {
        document
          .getElementsByClassName("search-img")
          [i].classList.toggle("display3");
          for(let j=0;j<Object.entries(chats)?.sort((a, b) => b[1].date - a[1].date).length;j++){
             let userChatInfoChats =
            document.getElementsByClassName("userChatInfoChats");
          userChatInfoChats[j].classList.toggle("display1");
          }
        }
    }
  };

  return (
    <>
      <div className="chats">
        {chats &&
          Object.entries(chats)
            ?.sort((a, b) => b[1].date - a[1].date)
            .map((chat) => (
              <div className="userChat" key={chat[0]}>
                <img
                  src={chat[1].userInfo.photoURL}
                  className="search-img"
                  onClick={() => open(chat[0])}
                  alt=""
                />
                <div
                  className="userChatInfoChats"
                  onClick={() => handleSelect(chat[1].userInfo)}
                >
                  <span className="chats-name">
                    {chat[1].userInfo.displayName}
                  </span>
                  <div className="latestMsg"></div>
                </div>
              </div>
            ))}
      </div>
    </>
  );
}
export default Chats;
