import insert from "../images/insert.png";
import img from "../images/img.png";
import send from "../images/send.png";

import "./input.css";
import { AuthContext } from "../context/AuthContext";
import { ChatContext } from "../context/ChatContext";
import { useContext, useState } from "react";
import {
  arrayUnion,
  doc,
  Timestamp,
  updateDoc,
} from "firebase/firestore";
import { v4 as uuid } from "uuid";
import { db, storage } from "../firebase";
import { getDownloadURL, ref, uploadBytesResumable } from "firebase/storage";
import MessageChat from "./MessageChat";
import UserInfoPage from "./UserInfoPage";


function Input() {
  const [text, setText] = useState("");
  const [image, setImage] = useState(null);

  const { currentUser } = useContext(AuthContext);
  const { data } = useContext(ChatContext);
  const handleSend = async () => {
    if (image) {
      console.log(image)
      const storageRef = ref(storage, uuid());

      const uploadTask = uploadBytesResumable(storageRef, image);

      uploadTask.on(
        // "state_changed",
        (error)=>{
          
        },
        () => {
          getDownloadURL(uploadTask.snapshot.ref).then(async (downloadURL) => {
            await updateDoc(doc(db, "chats", data.chatId), {
              messages: arrayUnion({
                id: uuid(),
                text,
                senderId: currentUser.uid,
                date: Timestamp.now(),
                image: downloadURL,
              }),
            });
          });
        }
      );
    } else {
      await updateDoc(doc(db, "chats", data.chatId), {
        messages: arrayUnion({
          id: uuid(),
          text,
          senderId: currentUser.uid,
          date: Timestamp.now(),
        }),
      });
    }
    //  await updateDoc(doc(db, "userChats",currentUser.uid),{
      //   [data.chatId]:{
        //       lastMessage:{
          //           text,
          //       },
          //       date:serverTimestamp(),
          //   },
          //  });
          //  await updateDoc(doc(db, "userChats",data.user.uid),{
            //   [data.chatId]:{
              //       lastMessage:{
                //           text,
                //       },
                //       date:serverTimestamp(),
                //   },
                //  });
                setText("");
                document.getElementsByClassName("type-something")[0].value = "";
                setImage(null);
              };
    const handleKey = async (e)=>{
      await e.code === "Enter" && handleSend();
    };
    // console.log(currentUser.displayName)
    return (
      <div className="inputBox">
      <div className="repliedMsgBox">
        yes {/* {messages["messages"]!=="" && messages["messages"]} */}
      </div>
      <div className="input-box">
        <input
          type="text"
          placeholder="Type something..."
          value={text} onKeyDown={handleKey}
          onChange={(e) => setText(e.target.value)}
          className="type-something"
          autoFocus/>
        <div className="send">
          <img src={insert} alt="" className="img-insert" />
          <input
            type="file"
            style={{ display: "none" }}
            id="file"
            onChange={(e) => setImage(e.target.files[0])}
          />
          <label htmlFor="file">
            <img src={img} alt="file" className="img-img" />
          </label>
          <button className={`send-btn `} onClick={handleSend}>
            <img src={send} alt="send" className="send-img" />
          </button>
        </div>
      </div>
      <MessageChat daata={text}/>

    </div>
  );
}
export default Input;
