import './components.css'
import { useContext, useEffect, useState } from 'react';
import { collection, query, where,getDocs, setDoc,doc, updateDoc, serverTimestamp, getDoc, onSnapshot } from "firebase/firestore";
import {db} from '../firebase'
import { AuthContext } from '../context/AuthContext';

function Search(){
    const [userName, setUserName] = useState("")
    const [user, setUser] = useState(null)
    const [err, setErr] = useState(false)
    const {currentUser} = useContext(AuthContext);
    const [chats,setChats] = useState([])
    const handleSearch = async ()=>{
        document.getElementsByClassName("chats")[0].classList.add("chatsDisplay")
        
       const q = query(collection(db,"users"),
                 where("displayName","==",userName)
    );

    
    try{

        const querySnapshot = await getDocs(q);
        querySnapshot.forEach((doc) => {
            setUser(doc.data())
        });
    }catch(err){
        setErr(true)
    }
    };

    
    const handleKey = (e)=>{
        e.code === "Enter" && handleSearch();
    };
    const handleSelect = async ()=>{
    //check whether the group(chats in firestore) exists, if not create
    const combinedId = currentUser.uid > user.uid ? currentUser.uid + user.uid : user.uid + currentUser.uid; 
    try{
        const res = await getDoc(doc(db,"chats",combinedId))

        if(!res.exists()){
            //create a chat in chats collection
            await setDoc(doc(db,"chats",combinedId),{messages:[]})
            //create user chats
            await updateDoc(doc(db,"userChats",currentUser.uid),{
                [combinedId]:{
                    userInfo:{
                        uid:user.uid,
                        displayName:user.displayName,
                        photoURL:user.photoURL,
                    },
                    date:serverTimestamp(),
                }
            });
            console.log("pp")
            //for other user
            await updateDoc(doc(db,"userChats",user.uid),{
                [combinedId]:{
                    userInfo:{
                        uid:currentUser.uid,
                        displayName:currentUser.displayName,
                        photoURL:currentUser.photoURL,
                    },
                    date:serverTimestamp(),
                }
            });
            console.log("hello")
        }
    }catch(err){}
    setUser(null)
    setUserName("")
    console.log("hi")
    document.getElementsByClassName("chats")[0].classList.remove("chatsDisplay")
    
}

useEffect(()=>{
    const getChats = ()=>{
      const unsub = onSnapshot(doc(db, "users", currentUser.uid),(doc) => {
          setChats(doc.data());   
      });
      return ()=>{
          unsub();
      };
    };
    currentUser.uid && getChats();
  },[currentUser.uid])
//   console.log(res)

    return (
        <div className='search'>
           <div className="searchForm" >
            <input type="text" placeholder='find someone'  value={userName} onKeyDown={handleKey} onChange={(e)=>setUserName(e.target.value)} className='search-input' />
            <span className="srch-btn" style={{display:"flex",alignItems:"center",position:"relative",float:"right",color:"#96a9a2",fontSize:"24px"}} onClick={handleSearch} >&#10097;</span>
           </div>
           {err && <span>user not found!</span>}
           {user  && user.uid!==currentUser.uid  &&  <div className='userChat' onClick={handleSelect}>
            <img src={user.photoURL} className='search-img' alt="" />
            <div className="userChatInfo">
                <span>{user.displayName}</span>
            </div>
           </div>}
        </div>
    )
}
export default Search;