import React, { useState } from "react";
import { createUserWithEmailAndPassword, updateProfile } from "firebase/auth";
import { auth, storage, db } from "../firebase";
import { ref, uploadBytesResumable, getDownloadURL } from "firebase/storage";
import { doc, setDoc } from "firebase/firestore";
import { useNavigate } from "react-router-dom";
import "./Register.css";
// import user from "../images/user.png"
function Register() {
  const [err, setErr] = useState(false);
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    let displayName = e.target[0].value;
    const email = e.target[1].value;
    const password = e.target[2].value;
    const file = e.target[3].files[0];
    // console.log(e.target[3].files[0])
    // console.log(user)
    try {
      const res = await createUserWithEmailAndPassword(auth, email, password);
      
      const storageRef = ref(storage, displayName);
      
      const uploadTask = uploadBytesResumable(storageRef, file);
      
      uploadTask.on(
        "state_changed",

        // (err) => {
          //  setErr(err)
          // },
          () => {
            getDownloadURL(uploadTask.snapshot.ref).then(async (downloadURL) => {
              await updateProfile(res.user, {
                displayName,
                photoURL: downloadURL,
              });
              await setDoc(doc(db, "users", res.user.uid), {
                uid: res.user.uid,
                displayName,
                email,
                photoURL: downloadURL,
              });
              
              await setDoc(doc(db, "userChats", res.user.uid), {});
              navigate("/");
          });
        }
      );
    } catch (err) {
      setErr(true);
    }
  };

  return (
    <div>
      {/* <div className="formContainer"> */}
          <div className="type">Register</div>
        <div className="inputs">
          <form onSubmit={handleSubmit}>
            <div className="input">
              <label className="label">Name</label>
              <input type="text" />
            </div>
            <div className="input">
              <label className="label">Email</label>
              <input type="email" />
            </div>
            <div className="input">
              <label className="label">Password</label>
              <input type="password" />
            </div>
            <div className="input">
                <label htmlFor="file" className="add-profile-label label">
                  Add Profile Image
                </label>
                <input type="file" className="insertfile" />
            </div>
            <div className="input">
            <button className="submit">Sign Up</button>
            </div>
            <a href="/login" className="HaveOrNotAccount">Already have an Account ?</a>
            {err && <span style={{color:"white"}}>Something went wrong</span>}
          </form>
        </div>
      {/* </div> */}
    </div>
  );
}
export default Register;
