import { getAuth, updateProfile } from "firebase/auth";
import { doc, onSnapshot, updateDoc } from "firebase/firestore";
import { useContext, useEffect, useState } from "react";
import { AuthContext } from "../context/AuthContext";
import { auth, db } from "../firebase";
import editImg from "../images/editImg.png"
// import { useRefresh } from 'react-admin';

const UserInfoPage = () => {
  const [chats, setChats] = useState([]);
  const { currentUser } = useContext(AuthContext);
  useEffect(() => {
    const getChats = () => {
      const unsub = onSnapshot(doc(db, "users", currentUser.uid), (doc) => {
        setChats(doc.data());
      });
      return () => {
        unsub();
      };
    };
    currentUser.uid && getChats();
  }, [currentUser.uid]);
  // const [image, setImage] = useState(currentUser.photoURL);
  const cut = () => {
    document
      .getElementsByClassName("nameUpdateForm")[0]
      .classList.remove("display5");
  };
  const ShowFrom = () => {
    document
      .getElementsByClassName("nameUpdateForm")[0]
      .classList.add("display5");
    };
    const bigDP = () => {
      document.getElementsByClassName("Details")[0].classList.toggle("display1");
      document.getElementsByClassName("userDp")[0].classList.toggle("display4");
      document.getElementsByClassName("dp-edit")[0].classList.toggle("display1");

    };
    // const refresh = useRefresh();
  const handleSubmit = async (e) => {
    e.preventDefault();
    let newDisplayName = e.target[0].value;
    
    updateProfile(auth.currentUser, {
      displayName: newDisplayName
    })
    .then(() => {
        document.location.reload()
        // refresh();
      })
      .catch((error) => {
        console.log("error occured");
      });

  
  // auth.updateUser(currentUser.uid, {
  //   displayName: newDisplayName,
  //   // photoURL: 'http://www.example.com/12345678/photo.png',
  // })
  // .then(() => {
  //   // See the UserRecord reference doc for the contents of userRecord.
  //   console.log('Successfully updated user');
  // })
  // .catch((error) => {
  //   console.log('Error updating user:', error);
  // });
  };
  return (
    <>
      <div>
        <div className="userDetails">
          <div className="userInfoDp">
            <img
              className="userDp"
              onClick={bigDP}
              src={currentUser.photoURL}
              alt="DP"
            />
            
          <label htmlFor="file">
                <img className="dp-edit" src={editImg} alt="" />
          </label>
          </div>
          <div className="Details">
            <div>
              <label className="user-name-label">Name:</label>
              <div className="edit-btn" onClick={ShowFrom} style={{}}>
                edit
              </div>
              <div className="userInfoName">{currentUser.displayName}</div>
            </div>
            <label className="user-name-label">Email:</label>
            <div className="userInfoEmail">{currentUser.email}</div>
          </div>
        </div>
        <form onSubmit={handleSubmit} className="nameUpdateForm">
          <div className="cut-btn" onClick={cut}>
            x
          </div>
          <input
            style={{ padding: "10px 15px", fontSize: "18px" }}
            placeholder="Enter the name"
            type="text"
            name=""
            id=""
          />
          <button
            style={{
              backgroundColor: "#434645",
              border: "none",
              padding: "10px 15px",
              fontSize: "18px",
              borderRadius: "10px",
              color: "#96a9a2",
            }}
          >
            Save
          </button>
        </form>
        {/* /// */}
        
      </div>
    </>
  );
};

export default UserInfoPage;
