import { signOut } from "firebase/auth";
import { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { ChatContext } from "../context/ChatContext";
import { auth } from "../firebase";

function MessageChat(props) {
  const navigate = useNavigate();
  const { data } = useContext(ChatContext);
  const onClick = () => {
    navigate("/");
  };
 
  return (
    <div className="chatInfo">
      <div className="chatInfo-prsn">
        {/* <img src={backarrow} className="backarrow" alt="" onClick={onClick} /> */}
        <span className="backarrow" onClick={onClick} >&#10094;</span>
        <img className="chat-user-icon " src={data.user?.photoURL} alt="" />
        <span>
          <div className="person-name">{data.user?.displayName}</div>
          <div className={`${props.daata ? "typing" : "display1"}  `}>typing...</div>
        </span>
      </div>
      <span>
        {/* <button onClick={() => signOut(auth)} className="logout-btn">
          Logout
        </button> */}
      </span>
    </div>
  );
}
export default MessageChat;
