import "./components.css";
import { useContext, useEffect, useRef } from "react";
import { AuthContext } from "../context/AuthContext";
import { ChatContext } from "../context/ChatContext";
import React from "react";
import forword from "../images/forward.png";

const Message = (messages) => {
  const { currentUser } = useContext(AuthContext);
  const { data } = useContext(ChatContext);
  const date = messages.message.date;
  const ref = useRef();
  useEffect(() => {
    ref.current?.scrollIntoView({ behavior: "auto" });
  }, [messages]);

  // setting time of the message
  const toDateTime = () => {
    let t = new Date(1970, 0, 1);
    t.setSeconds(date.seconds);
    return t;
  };

  let min = toDateTime().getMinutes() + 30;
  let hour = toDateTime().getHours() + 5;
  if (min > 60) {
    min = min - 60;
    hour = hour + 1;
  }
  if (min < 10) {
    min = "0" + min;
  }
  if (hour > 12 && hour < 24) {
    hour = hour - 12;
  }
  if (hour >= 24) {
    hour = hour - 24;
  }
  if (hour < 10) {
    hour = "0" + hour;
  }
  let datee = new Date();
  let hr = datee.getHours();
  if (hr > 12) {
    hr = hr - 12;
  }
  if (hr < 10) {
    hr = "0" + hr;
  }
  let mins = datee.getMinutes();
  if (mins < 10) {
    mins = "0" + mins;
  }
  return (
    <>
      <div
        className={`message ${
          messages.message.text === "" && !messages.message.image
            ? "display1"
            : "display"
        }  ${messages.message.senderId === currentUser.uid && "owner"}`}
      >
        <div className={`messages-box`} ref={ref}>
          <div className="msg-prsn" style={{ display: "flex" }}>
            <div className={`messageInfo `}>
              <img
                className={`message-user-icon `}
                src={
                  messages.message.senderId === currentUser.uid
                    ? currentUser.photoURL
                    : data.user.photoURL
                }
                alt=""
              />
              <div
                className={`justnow ${
                  messages.message.senderId === currentUser.uid && "owner"
                }`}
              >{`${
                hr + ":" + mins === hour + ":" + min
                  ? "just now"
                  : hour + ":" + min
              }`}</div>
            </div>
          </div>
          <div
            className={`massageContent ${
              messages.message.text === "" && "display1"
            }`}
          >
            <p
              className={`textMessage ${
                messages.message.senderId === currentUser.uid && "owner owner1"
              }`}
            >
              {messages.senderId !== currentUser.uid && messages.message.text}
            </p>
            <img
              src={forword}
              alt="share"
              className={`${
                messages.message.senderId === currentUser.uid && "owner"
              } forword`}
            />
          </div>
          <div className={`ImageContent`}>
            {messages.message.image && (
              <img
                style={{ padding: "5px 0 5px 0" }}
                className={`message-img ${
                  messages.message.senderId === currentUser.uid && "owner"
                }`}
                src={messages.message.image}
                alt=""
              />
            )}
          </div>
        </div>
      </div>
    </>
  );
};
export default Message;
