import './components.css'
import { signOut } from 'firebase/auth'
import logo from "../images/logo.png"
import { auth, db } from '../firebase'
import { useContext, useEffect, useState } from 'react';
import { doc, onSnapshot } from 'firebase/firestore';
import { AuthContext } from '../context/AuthContext';
import {  useNavigate } from 'react-router-dom';

function Navbar(){

    const [chats,setChats] = useState([])
    const {currentUser} = useContext(AuthContext);
    const navigate = useNavigate();
    // const {dispatch} = useContext(ChatContext);
    const UserInfoPage = ()=>{
       navigate("/userinfo")
    }
    useEffect(()=>{
        const getChats = ()=>{
          const unsub = onSnapshot(doc(db, "users", currentUser.uid),(doc) => {
              setChats(doc.data());   
          });
          return ()=>{
              unsub();
          };
        };
        currentUser.uid && getChats();
      },[currentUser.uid])
    


    return (
        <>
        <div className='navbar-box'>
            <div className='UserInfoClass' onClick={UserInfoPage} style={{textAlign:"center",marginBottom:"10px"}}>
            <img style={{position:"relative",width:"20px",height:"20px",borderRadius:"50%",right:"10px",alignItems:"center",top:"3px"}} src={chats.photoURL} alt="" />
            <div style={{position:"relative",color:"#96a9a2"}}>{`Hi ${chats.displayName}`}</div>
            </div>
            <div className='App-Name'>
                <img style={{width:"90px",height:"60px"}} src={logo} alt="" />
            </div>
            <div onClick={()=>signOut(auth)} style={{position:"relative",color:"#96a9a2",textAlign:"center",top:"-10px"}}>logout</div>
        </div>
            </>
    )
}
export default Navbar;